import { connectAsync, MqttClient as Client } from 'mqtt';
import { ScryptedDeviceBase } from '@scrypted/sdk';

export default class MqttClient {
    mqttClient: Client;
    mqttPathmame: string;
    host: string;
    username: string;
    password: string;
    console: Console;
    topicLastValue: Record<string, any> = {};
    logger: Console;

    constructor(host: string, username: string, password: string, logger: Console) {
        this.host = host;
        this.username = username;
        this.password = password;
        this.logger = logger;
    }

    async disconnect() {
        if (this.mqttClient) {
            try {
                this.mqttClient.end(true);
            } catch (e) {
                this.logger.log('Error closing MQTT connection', e);
            }
        }
    }

    async getMqttClient(forceReconnect?: boolean): Promise<Client> {
        const _connect = async () => {
            const client = await connectAsync(this.mqttPathmame, {
                rejectUnauthorized: false,
                username: this.username,
                password: this.password,
                reschedulePings: true
            });

            client.on('connect', data => {
                this.mqttClient = client;
            });

            client.on('error', data => {
                this.logger.log('Error connecting to mqtt', data);
                this.mqttClient = undefined;
            });

            client.setMaxListeners(Infinity);

            this.logger.log('Connected to mqtt');
            this.mqttClient = client;
        }

        if (!this.mqttClient || forceReconnect) {
            if (forceReconnect && this.mqttClient) {
                try {
                    await this.mqttClient.endAsync();
                } catch (e) { }
            }
            const url = this.host;
            const urlWithoutPath = new URL(url);
            urlWithoutPath.pathname = '';

            this.mqttPathmame = urlWithoutPath.toString();
            if (!this.mqttPathmame.endsWith('/')) {
                this.mqttPathmame = `${this.mqttPathmame}/`;
            }
            this.logger.log('Starting MQTT connection', this.host, this.username, this.mqttPathmame);

            await _connect();

            if (this.mqttClient) {
                this.mqttClient.onKeepaliveTimeout = async () => {
                    this.logger.log('keepalive timeout. Reconnecting');
                    this.mqttClient.reconnect();
                }
            }
            return this.mqttClient;
        } else if (!this.mqttClient.connected) {
            this.logger.log('MQTT disconnected. Reconnecting', this.host, this.username, this.mqttPathmame);

            await _connect();
            return this.mqttClient;
        } else {
            return this.mqttClient;
        }
    }

    async publish(topic: string, inputValue: any, retain = true) {
        let value;
        try {
            if (typeof inputValue === 'object')
                value = JSON.stringify(inputValue);
            if (inputValue.constructor.name !== Buffer.name)
                value = inputValue.toString();
        } catch (e) {
            this.logger.log(`Error parsing publish values: ${JSON.stringify({ topic, value })}`, e);
            return;
        }

        if (retain && this.topicLastValue[topic] === value) {
            this.logger.debug(`Skipping publish, same as previous value: ${JSON.stringify({ topic, value, previousValue: this.topicLastValue[topic] })}`);

            return;
        }

        this.logger.debug(`Publishing ${JSON.stringify({ topic, value })}`);
        const client = await this.getMqttClient();
        try {
            await client.publishAsync(topic, value, { retain });
        } catch (e) {
            this.logger.log(`Error publishing to MQTT. Reconnecting. ${JSON.stringify({ topic, value })}`, e);
            await this.getMqttClient(true);
            await client.publishAsync(topic, value, { retain });
        } finally {
            if (retain) {
                this.topicLastValue[topic] = value;
            }
        }
    }

    public getMqttTopicTopics<Domain extends string = string>(pluginName: string, device: ScryptedDeviceBase) {
        const deviceId = device.id;

        const getEntityTopic = (entity: string) => `scrypted/${pluginName}/${deviceId}/${entity}`;
        const getInfoTopic = (entity: string) => `${getEntityTopic(entity)}/info`;
        const getDiscoveryTopic = (domain: Domain, entity: string) => `homeassistant/${domain}/${pluginName}-${deviceId}/${entity}/config`;

        return {
            getEntityTopic,
            getDiscoveryTopic,
            getInfoTopic,
        }
    }

    async subscribeToTopic(topic: string, cb: (message: any) => void) {
        const client = await this.getMqttClient();
        await client.unsubscribeAsync([topic]);
        await client.subscribeAsync([topic]);

        client.on('message', (messageTopic, message) => {
            const messageString = message.toString();
            if (messageTopic === topic) {
                cb(messageString);
            }
        })
    }

    async subscribe(topics: string[], cb: (topic: string, message: string) => Promise<void>) {
        await this.unsubscribe(topics);
        await this.mqttClient.subscribeAsync(topics);

        this.mqttClient.on('message', async (topic, message) => {
            const messageString = message.toString();
            await cb(topic, messageString)
        });
    }

    async unsubscribe(topics: string[]) {
        await this.mqttClient.unsubscribeAsync(topics);
    }
}
