import sdk, { ScryptedDeviceBase, ScryptedDeviceType, ScryptedInterface, Setting, Settings, SettingValue, WritableDeviceState } from "@scrypted/sdk";
import { StorageSetting, StorageSettings, StorageSettingsDict } from "@scrypted/sdk/storage-settings";
import MqttClient from './mqtt-client';
import { BaseMixin } from "./baseMixin";
const { systemManager } = sdk;

export type BaseSettingsKey = 'pluginEnabled'
    | 'mqttEnabled'
    | 'debug'
    | 'devNotifier'
    | 'useMqttPluginCredentials'
    | 'mqttHost'
    | 'mqttUsename'
    | 'mqttPassword'

export const getBaseSettings = (props: {
    onPluginSwitch?: StorageSetting['onPut'],
    hideMqtt?: boolean,
}) => {
    const { onPluginSwitch, hideMqtt } = props;
    const settings: StorageSettingsDict<BaseSettingsKey> = {
        pluginEnabled: {
            title: 'Plugin enabled',
            type: 'boolean',
            defaultValue: true,
            immediate: true,
            group: 'base',
            onPut: onPluginSwitch
        },
        mqttEnabled: {
            title: 'MQTT enabled',
            type: 'boolean',
            defaultValue: true,
            immediate: true,
            group: 'base',
            hide: hideMqtt
        },
        debug: {
            title: 'Log debug messages',
            type: 'boolean',
            defaultValue: false,
            immediate: true,
            group: 'base',
        },
        devNotifier: {
            title: 'DEV notifier',
            type: 'device',
            deviceFilter: `(type === '${ScryptedDeviceType.Notifier}')`,
            group: 'base',
        },
        useMqttPluginCredentials: {
            title: 'Use MQTT plugin credentials',
            subgroup: 'MQTT',
            type: 'boolean',
            immediate: true,
            group: 'base',
            hide: hideMqtt
        },
        mqttHost: {
            title: 'Host',
            subgroup: 'MQTT',
            description: 'Specify the mqtt address.',
            placeholder: 'mqtt://192.168.1.100',
            group: 'base',
            hide: hideMqtt
        },
        mqttUsename: {
            title: 'Username',
            subgroup: 'MQTT',
            description: 'Specify the mqtt username.',
            group: 'base',
            hide: hideMqtt
        },
        mqttPassword: {
            title: 'Password',
            subgroup: 'MQTT',
            description: 'Specify the mqtt password.',
            type: 'password',
            group: 'base',
            hide: hideMqtt
        },
    };

    return settings;
};

export class BasePlugin extends ScryptedDeviceBase implements Settings {
    public mqttClient: MqttClient;
    private mainLogger: Console;
    public currentMixinsMap: Record<string, BaseMixin> = {};
    public storageSettings: StorageSettings<BaseSettingsKey>;


    constructor(nativeId: string, public opts: {
        pluginFriendlyName: string,
        mixinGroup?: string,
        mixinKey?: string,
    }) {
        super(nativeId);
    }

    async onInit() {
        try {
            await this.setupMqttClient();
        } catch (e) {
            this.getLogger().log('Error onInit', e);
        }
    }

    private async setupMqttClient() {
        const { mqttEnabled, useMqttPluginCredentials, pluginEnabled } = this.storageSettings.values;
        if (mqttEnabled && pluginEnabled && !this.storageSettings.settings.mqttEnabled.hide) {
            let mqttHost: string;
            let mqttUsename: string;
            let mqttPassword: string;

            const logger = this.getLogger();

            if (this.mqttClient) {
                this.mqttClient.disconnect();
                this.mqttClient = undefined;
            }

            if (useMqttPluginCredentials) {
                logger.log(`Using MQTT plugin credentials.`);
                const mqttDevice = systemManager.getDeviceByName('MQTT') as unknown as Settings;
                const mqttSettings = await mqttDevice.getSettings();

                const isInternalBroker = (JSON.parse(mqttSettings.find(setting => setting.key === 'enableBroker')?.value as string || 'false')) as boolean;

                if (isInternalBroker) {
                    logger.log(`Internal MQTT broker not supported yet. Please disable useMqttPluginCredentials.`);
                } else {
                    mqttHost = mqttSettings.find(setting => setting.key === 'externalBroker')?.value as string;
                    mqttUsename = mqttSettings.find(setting => setting.key === 'username')?.value as string;
                    mqttPassword = mqttSettings.find(setting => setting.key === 'password')?.value as string;
                }
            } else {
                logger.log(`Using provided credentials.`);

                mqttHost = this.storageSettings.getItem('mqttHost');
                mqttUsename = this.storageSettings.getItem('mqttUsename');
                mqttPassword = this.storageSettings.getItem('mqttPassword');
            }

            if (!mqttHost || !mqttUsename || !mqttPassword) {
                logger.log('MQTT params not provided');
            }

            try {
                this.mqttClient = new MqttClient(mqttHost, mqttUsename, mqttPassword, logger);
            } catch (e) {
                this.getLogger().log('Error setting up MQTT client', e);
            }
        }
    }

    async getMqttClient() {
        if (!this.mqttClient) {
            await this.setupMqttClient();
        }

        return this.mqttClient;
    }

    async getMixin(mixinDevice: any, mixinDeviceInterfaces: ScryptedInterface[], mixinDeviceState: WritableDeviceState): Promise<any> {
        const props = {
            mixinDevice,
            mixinDeviceInterfaces,
            mixinDeviceState,
            mixinProviderNativeId: this.nativeId,
            group: this.opts.mixinGroup,
            groupKey: this.opts.mixinKey,
        };

        if (
            [ScryptedInterface.Camera, ScryptedInterface.VideoCamera,].some(int => mixinDeviceInterfaces.includes(int))
        ) {
            return new BaseMixin(
                props,
                this
            );
        } else if (
            [ScryptedInterface.BinarySensor, ScryptedInterface.Lock].some(int => mixinDeviceInterfaces.includes(int))
        ) {
            return new BaseMixin(
                props,
                this
            );
        } else if (mixinDeviceInterfaces.includes(ScryptedInterface.Notifier)) {
            return new BaseMixin(
                props,
                this
            );
        }
    }

    async releaseMixin(id: string, mixinDevice: any): Promise<void> {
        await mixinDevice.release();
    }


    async getSettings(): Promise<Setting[]> {
        return await this.storageSettings.getSettings();
    }

    putSetting(key: string, value: SettingValue): Promise<void> {
        return this.storageSettings.putSetting(key, value);
    }

    getLogger(): Console {
        if (!this.mainLogger) {
            const prefix = `[${this.opts.pluginFriendlyName}]`;
            const log = (type: 'log' | 'debug' | 'error', message?: any, ...optionalParams: any[]) => {
                const now = new Date().toLocaleString();
                if (type !== 'debug' || this.storageSettings.getItem('debug')) {
                    this.console.log(`${prefix} ${now} - `, message, ...optionalParams);
                }
                if (type === 'error' && this.storageSettings.values.devNotifier) {
                    this.storageSettings.values.devNotifier.sendNotification(this.opts.pluginFriendlyName, {
                        body: `${message}${optionalParams?.map(param => '\n' + param)}`,
                        data: {
                            pushover: {
                                priority: 1
                            }
                        }
                    })
                }
            };

            this.mainLogger = {
                log: (message?: any, ...optionalParams: any[]) => log('log', message, ...optionalParams),
                debug: (message?: any, ...optionalParams: any[]) => log('debug', message, ...optionalParams),
                error: (message?: any, ...optionalParams: any[]) => log('error', message, ...optionalParams),
            } as Console
        }

        return this.mainLogger
    }
}

