import sdk, { Settings, MixinDeviceBase, MixinDeviceOptions } from "@scrypted/sdk";
import { StorageSettings } from "@scrypted/sdk/storage-settings";
import { BasePlugin } from "./basePlugin";

export class BaseMixin extends MixinDeviceBase<Settings> {
    logger: Console;
    killed: boolean;
    baseStorageSettings = new StorageSettings(this, {
        debug: {
            title: 'Log debug messages',
            type: 'boolean',
            defaultValue: false,
            immediate: true,
        },
    });

    constructor(
        options: MixinDeviceOptions<Settings>,
        public plugin: BasePlugin
    ) {
        super(options);

        this.plugin.currentMixinsMap[this.name] = this;
    }
    async release() {
        this.killed = true;
    }

    private getLogger() {
        const deviceConsole = sdk.deviceManager.getMixinConsole(this.id, this.nativeId);

        if (!this.logger) {
            const log = (debug: boolean, message?: any, ...optionalParams: any[]) => {
                const now = new Date().toLocaleString();
                if (!debug || this.baseStorageSettings.getItem('debug')) {
                    deviceConsole.log(` ${now} - `, message, ...optionalParams);
                }
            };
            this.logger = {
                log: (message?: any, ...optionalParams: any[]) => log(false, message, ...optionalParams),
                debug: (message?: any, ...optionalParams: any[]) => log(true, message, ...optionalParams),
            } as Console
        }

        return this.logger;
    }
}